<template>
  <div>
    <!-- 引入标题 -->
    <NavTitle></NavTitle>

    <div class="waw_practice_container">
      <div class="waw_hidden"></div>
      <div class="waw_grid_container">
        <van-grid :column-num="4" :border="false">
          <van-grid-item
            v-for="(item, index) in girdData"
            :key="index"
            @click="onClick(index)"
            :icon="item.imgIcon"
            :text="item.title"
          />
        </van-grid>
      </div>
      <div class="waw_hidden"></div>
      <div class="waw_dry">
        <div class="waw_dray_box">
          <p>近期模考</p>
          <div class="waw_dray_sort">
            <span class="waw_dray_sort_box">默认排序</span>
            <span>时间</span>
          </div>
          <div class="waw_empty_img">
            <img src="https://wap.365msmk.com/img/empty@2x.a2fa524d.png" />
          </div>
          <p class="waw_op_box"><small>暂无模考安排，敬请期待</small></p>
        </div>
      </div>
      <div class="waw_hidden"></div>
    </div>
  </div>
</template>
<script>
import NavTitle from "../../components/navTitle/Title"; //引入导航子组件
export default {
  components: {
    NavTitle, //注册导航子组件
  },
  data() {
    return {
      girdData: [
        {
          imgIcon:
            "data:image/png;base64,",
          title: "考点专练",
        },
        {
          imgIcon:
            "data:image/png;base64,",
          title: "套卷练习",
        },
        {
          imgIcon:
            "data:image/png;base64,",
          title: "仿真模考",
        },
        {
          imgIcon:
            "data:image/png;base64,",
          title: "错题练习",
        },
        {
          imgIcon:
            "data:image/png;base64,",
          title: "测评记录",
        },
        {
          imgIcon:
            "data:image/png;base64,",
          title: "习题收藏",
        },
      ],
    };
  },
  methods: {
    onClick(index) {
      //点击宫格跳转对应页面
      var path = "";
      var token = localStorage.getItem("token");
      switch (index) {
        case 0:
          path = "/examinationPlace";
          break;
        case 1:
          path = "/setExercises";
          break;
        case 2:
          path = "/simulationTest";
          break;
        case 3:
          if (token) {
            path = "/wrongExercises";
          } else {
            path = "/login";
          }
          break;
        case 4:
          if (token) {
            path = "/evaluationRecord";
          } else {
            path = "/login";
          }
          break;
        case 5:
          if (token) {
            path = "/exerciseCollection";
          } else {
            path = "/login";
          }
          break;
      }
      this.$router.push(path);
    },
  },
};
</script>
<style lang='scss' scoped>
.waw_practice_container {
  width: 100%;
  // height: 84vh;
  background: whitesmoke;
}
.waw_grid_container {
  width: 96%;
  margin: 0 auto;
  background: white;
}
.waw_hidden {
  width: 100%;
  height: 0.8rem;
}
.waw_dry {
  width: 100%;
  background: white;
  .waw_dray_box {
    width: 96%;
    margin: 0 auto;
    height: 100%;
    p {
      width: 100%;
      height: 1.5rem;
      color: gray;
      display: inline-flex;
      align-items: center;
      font-size: 0.65rem;
    }
    .waw_dray_sort {
      width: 100%;
      height: 1.5rem;
      display: inline-flex;
      justify-content: space-between;
      align-items: center;
      font-size: 0.65rem;
      .waw_dray_sort_box {
        color: orange;
      }
    }
    .waw_empty_img {
      width: 100%;
      height: 10rem;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      img {
        width: 30%;
        height: 90%;
      }
    }
    .waw_op_box {
      width: 100%;
      height: 2rem;
      display: inline-flex;
      justify-content: center;
      align-items: center;
    }
  }
}
</style>